import React, { useState, useEffect, useCallback } from "react";
import "./header.scss";
import Hamburger from "../hamburger/Hamburger";
import Sidebar from "../sidebar/Sidebar";
import { NavLink } from "react-router-dom";
import useScrollEvent from "../../hooks/useScrollEvent";
import { useHistory } from "react-router-dom";
import categoryAtom from "../../atoms/categoryAtom";
import { useRecoilState } from "recoil";

const Header = () => {
  //logoya tıklanınca tüm postları listelemek için
  const [, setSelectCategory] = useRecoilState(categoryAtom);

  const history = useHistory();

  /// sayfa yenilenince url e göre seçili nav item ayarlamak için
  const pathDinle = useCallback(() => {
    if (history.location.pathname.startsWith("/how-to-do/")) {
      //howToDo+dinamik root
      return 3;
    }
    if (history.location.pathname === "/how-to-do") {
      //howToDo page
      return 2;
    } else {
      return 1; //anasayfa
    }
  }, [history.location.pathname]);

  const [selected, setSelected] = useState(pathDinle()); //seçili nav item

  const [locationKeys, setLocationKeys] = useState([]); // browser ileri geri butonuna tıklanınca diziye eleman ekler çıkarır.değişmişse ileri geri yapılmış demektir.

  useEffect(() => {
    /// browser ileri geri yapınca locationKeys dizisini ayarlar
    return history.listen((location) => {
      if (history.action === "PUSH") {
        setLocationKeys([location.key]);
      }
      if (history.action === "POP") {
        if (locationKeys[1] === location.key) {
          setLocationKeys(([_, ...keys]) => keys);
        } else {
          setLocationKeys((keys) => [location.key, ...keys]);
        }
      }
    });
  }, [history, locationKeys]);

  useEffect(() => {
    //locationKeys dizisi değişince url e bakıp seçili nav item i ayarlar
    setSelected(pathDinle());
  }, [locationKeys, pathDinle]);

  /// hamburger menu control
  const { scroll } = useScrollEvent();
  const [hamburger, setHamburger] = useState(false);
  useEffect(() => {
    if (hamburger) {
      if (scroll > 5) {
        setHamburger(false);
      }
    }
  }, [scroll, hamburger]);

  const handleHamburger = () => {
    setHamburger(!hamburger);
  };

  const logoOnClick = () => {
    setSelectCategory(0);
  };

  return (
    <div className="navbar">
      <NavLink to="/how-to-do" className="logo" onClick={() => logoOnClick()} />
      <ul className="navbar-nav header-component-ic">
        <li
          className={`nav-item ${selected === 1 && "selectedItem"}`}
          onClick={() => setSelected(1)}
        >
          <NavLink className="nav-link" to="/">
            Who Am I
          </NavLink>
        </li>
        <li
          className={`nav-item ${selected === 2 && "selectedItem"}`}
          onClick={() => setSelected(2)}
        >
          <NavLink className="nav-link" to="/how-to-do">
            How To Do
          </NavLink>
        </li>
      </ul>
      <Sidebar
        selectedNav={selected}
        selectedFunc={setSelected}
        isOpen={hamburger}
      />
      <Hamburger hamburger={hamburger} hamburgerFunc={handleHamburger} />
    </div>
  );
};

export default Header;
