import React, { useEffect } from "react";
import PropTypes from "prop-types";
import "./postContent.scss";
import { NavLink } from "react-router-dom";
import PreCode from "../preCode/PreCode";
import Prism from "prismjs";
import "../preCode/prism-gruvbox-dark.css";
import ReactPlayer from "react-player";

const PostContent = ({ post }) => {
  useEffect(() => {
    Prism.highlightAll();
  }, [post.contents]);

  const typeFunc = (i, index) => {
    if (i.tag === "p") {
      if (i.content.includes("!!!")) {
        const contentArr = i.content.split("!!!");
        const linkArr = contentArr[1].split("*%*");
        return (
          <p key={index} className={i.class}>
            {contentArr[0]}
            <NavLink key={index * 100} className="p-link" to={linkArr[0]}>
              {linkArr[1]}
            </NavLink>
            {contentArr[2]}
          </p>
        );
      }
      return (
        <p key={index} className={i.class}>
          {i.content}
        </p>
      );
    }

    if (i.tag === "div") {
      return <div key={index} className={i.class}></div>;
    }
    if (i.tag === "code") {
      return <PreCode key={index} code={i.content} language="js" />;
    }
  };

  return (
    <div className="post-content-container">
      <div className="post-title"> {post.title.toUpperCase()} </div>
      <ReactPlayer
        url={post.video}
        playing={true}
        loop={true}
        volume={0}
        muted={true}
        width="100%"
        height="auto"
      />

      <div className="post-content-inset-container">
        {post.content.map((i, index) => typeFunc(i, index))}
      </div>
    </div>
  );
};
PostContent.propTypes = {
  post: PropTypes.object.isRequired,
};
PostContent.defaultProps = {
  post: {},
};

export default PostContent;
