import { atom } from "recoil";

const x = sessionStorage.getItem("selectedCategory");
const categoryAtom = atom({
  key: "categoryAtom",
  default:
    x === ""
      ? 0
      : x === "css"
      ? 1
      : x === "react"
      ? 2
      : x === "style"
      ? 3
      : x === "native"
      ? 4
      : 0,
});

export default categoryAtom;
