import "./app.scss";
import Header from "./components/header/Header";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import NotFound from "./components/NotFound";
import Home from "./components/home/Home";
import HowToDo from "./components/howToDo/HowToDo";
import Footer from "./components/footer/Footer";
import Post from "./components/content/Post";
import AddPost from "./components/addPost/AddPost";

function App() {
  return (
    <Router>
      <div className="app-container">
        <div className="header">
          <Header />
        </div>
        <div className="main" style={{ color: "white" }}>
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/how-to-do" exact component={HowToDo} />
            <Route path="/how-to-do/:title" exact component={Post} />
            <Route path="/add-post" exact component={AddPost} />
            <Route component={NotFound} />
          </Switch>
        </div>
        <div className="footer">
          <Footer />
        </div>
      </div>
    </Router>
  );
}

export default App;
