import React from "react";
import "./howToDoHeader.scss";
import { FaReact } from "react-icons/fa";
import PropTypes from "prop-types";

const HowToDoHeader = ({ dropdownSelected, setDropdownSelected }) => {
  return (
    <div className="content-header-box">
      <div className="header-box-sup-tittle-one header-box-elements">
        <div
          className={
            dropdownSelected !== 0
              ? dropdownSelected <= 2
                ? "header-box-sup-tittle-button header-box-sup-tittle-button-selected"
                : "header-box-sup-tittle-button"
              : "header-box-sup-tittle-button"
          }
        >
          <FaReact style={{ marginRight: "4px" }} /> React
        </div>
        <div className="header-box-sub-tittle-box ">
          <div style={{ height: "22px" }}></div>
          <div
            className={
              dropdownSelected === 1
                ? "header-box-sub-tittle header-box-sub-tittle-selected"
                : "header-box-sub-tittle"
            }
            onClick={() => setDropdownSelected(1)}
          >
            CSS/Sass
          </div>
          <div
            className={
              dropdownSelected === 2
                ? "header-box-sub-tittle header-box-sub-tittle-selected"
                : "header-box-sub-tittle"
            }
            onClick={() => setDropdownSelected(2)}
          >
            React
          </div>
        </div>
      </div>
      <div className="header-box-sup-tittle-two header-box-elements">
        <div
          className={
            dropdownSelected >= 3
              ? "header-box-sup-tittle-button header-box-sup-tittle-button-selected"
              : "header-box-sup-tittle-button"
          }
          style={{ color: "#f72585" }}
        >
          <FaReact style={{ marginRight: "4px" }} /> React Native
        </div>
        <div className="header-box-sub-tittle-box">
          <div style={{ height: "22px" }}></div>
          <div
            className={
              dropdownSelected === 3
                ? "header-box-sub-tittle header-box-sub-tittle-selected"
                : "header-box-sub-tittle"
            }
            onClick={() => setDropdownSelected(3)}
            style={{ color: "#f72585" }}
          >
            Style
          </div>
          <div
            className={
              dropdownSelected === 4
                ? "header-box-sub-tittle header-box-sub-tittle-selected"
                : "header-box-sub-tittle"
            }
            onClick={() => setDropdownSelected(4)}
            style={{ color: "#f72585" }}
          >
            React Native
          </div>
        </div>
      </div>
    </div>
  );
};

HowToDoHeader.propTypes = {
  dropdownSelected: PropTypes.number.isRequired,
  setDropdownSelected: PropTypes.func.isRequired,
};
HowToDoHeader.defaultProps = {
  dropdownSelected: 0,
  setDropdownSelected: () => {},
};

export default HowToDoHeader;
