import React, { useEffect } from "react";
import "./hamburger.scss";
import useWindowWidthHeight from "../../hooks/useWindowWidthHeight";

const Hamburger = ({ hamburger, hamburgerFunc }) => {
  const { width } = useWindowWidthHeight();
  useEffect(() => {
    if (hamburger === true && width >= 768) {
      hamburgerFunc();
    }
  });

  return (
    <div className="hamburger" onClick={() => hamburgerFunc()}>
      <div className={hamburger ? "whileOpen" : "whileClose"}></div>
    </div>
  );
};

export default Hamburger;
