import React, { useState, useRef, useEffect } from "react";
import "./addPost.scss";
import { db } from "../../firebase-config";
import {
  addDoc,
  collection,
  doc,
  deleteDoc,
  getDocs,
  getDoc,
} from "firebase/firestore";
import {
  ref,
  uploadBytesResumable,
  getDownloadURL,
  getStorage,
  deleteObject,
} from "firebase/storage";
import LoadingAnimation from "../loadingAnimation/LoadingAnimation";

const AddPost = () => {
  const [cardTitle, setCardTitle] = useState("");
  const [cardImg, setCardImg] = useState("");
  const [cardIcon, setCardIcon] = useState("");
  const [category, setCategory] = useState("");
  const [content, setContent] = useState([]);
  const [reactIconSelect, setReactIconSelect] = useState(false);
  const [cssIconSelect, setCssIconSelect] = useState(false);
  const [cate, setCate] = useState("");
  const [fontSize, setFontSize] = useState("p-size-md");
  const [pMidle, setPMidle] = useState(false);
  const [pNot, setPNot] = useState(false);
  const [ilerleme, setIlerleme] = useState(false);
  const [error, setError] = useState(false);
  const [video, setVideo] = useState(null);

  const baslikRef = useRef(null);
  const paragrafRef = useRef(null);
  const boslukRef = useRef(null);
  const codeRef = useRef(null);

  const baslikEkle = () => {
    setCardTitle(baslikRef.current.value);
  };

  const fileSelectedHandler = async (e) => {
    setCardImg(e.target.files[0]); //yüklenen dosya state de tutulur
  };

  const checkboxChange = (value) => {
    if (value === "css") {
      setCssIconSelect(!cssIconSelect);
    }
    if (value === "react") {
      setReactIconSelect(!reactIconSelect);
    }
  };

  const cardIconEkle = () => {
    if (reactIconSelect === true && cssIconSelect === false) {
      setCardIcon("react");
    }
    if (reactIconSelect === false && cssIconSelect === true) {
      setCardIcon("css");
    }
    if (reactIconSelect === true && cssIconSelect === true) {
      setCardIcon("react,css");
    }
    if (reactIconSelect === false && cssIconSelect === false) {
      return window.alert("icon seçilmedi");
    }
  };

  const categoryChange = (e) => {
    setCate(e.target.value);
  };

  const categoryEkle = () => {
    setCategory(cate);
  };

  const fontSizeChange = (e) => {
    setFontSize(e.target.value);
  };

  const addItem = (ref) => {
    let x = {};
    if (ref === paragrafRef) {
      x = {
        tag: "p",
        class: `${fontSize} ${pMidle === true ? "p-midle" : ""} ${
          pNot === true ? "p-sub-title" : ""
        }`,
        content: ref.current.value,
      };
    }
    if (ref === boslukRef) {
      x = {
        tag: "div",
        class: "post-space",
        content: null,
      };
    }
    if (ref === codeRef) {
      x = {
        tag: "code",
        class: null,
        content: ref.current.value,
      };
    }
    setContent([...content, x]);
  };
  const videoSetState = (e) => {
    e.preventDefault();
    setVideo(e.target.files[0]);
  };

  // video yükleme için
  const uploadFiles = (file) => {
    const storage = getStorage();
    const storageRef = ref(storage, `videos/${file.name}`);

    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        // video yüklenirken ilerleme çubuğu için
        const prog = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        console.log("video yüklenmesi % :", prog);
      },
      (error) => {
        // video yüklenirken hata varsa
        setError(true);
        console.log(error);
      },
      () => {
        // hata yoksa işlemin sonucu bize bir link verir
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          saveContentDB(downloadURL);
        });
      }
    );
  };

  const uploadImageFile = (contentId) => {
    //id=güncellenmesi gereken document id dir
    const file = cardImg;
    const storage = getStorage();
    const storageRef = ref(storage, `images/${file.name}`);

    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on(
      "state_changed",
      (snapshot) => {
        // resim yüklenirken ilerleme çubuğu için
        const prog = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        console.log("image yüklenmesi % :", prog);
      },
      (error) => {
        // resim yüklenirken hata varsa
        setError(true);
        console.log(error);
      },
      () => {
        // hata yoksa işlemin sonucu bize bir link verir
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          saveCardsDB(downloadURL, contentId);
        });
      }
    );
  };

  const saveCardsDB = async (imgUrl, contentId) => {
    const postsCollectionsRef = collection(db, "cards");
    try {
      const docRef = await addDoc(postsCollectionsRef, {
        title: cardTitle,
        img: imgUrl,
        category: category,
        icon: cardIcon,
        content: contentId,
      });

      setIlerleme(false);
      return window.alert("DB'ye EKLEME TAMAMLANDI ID :", docRef.id);
    } catch (e) {
      console.error("Error adding document: ", e);
    }
  };

  //dbye kaydet
  const saveContentDB = async (url) => {
    const postsCollectionsRef = collection(db, "contents");
    try {
      const docRef = await addDoc(postsCollectionsRef, {
        video: url,
        content: content,
      });

      uploadImageFile(docRef.id);
    } catch (e) {
      console.error("Error adding document: ", e);
    }
  };

  const dbKaydet = async () => {
    await uploadFiles(video);
    setIlerleme(true);
  };

  const [cards, setCards] = useState([]);
  useEffect(() => {
    const cardsCollectionsRef = collection(db, "cards");

    const getPosts = async () => {
      const card = await getDocs(cardsCollectionsRef);
      await setCards(card.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    };
    getPosts();
  }, []);

  const postSil = async (cardId, img, contentId) => {
    setIlerleme(true);

    await resimSil(cardId, img, contentId);
  };

  const resimSil = async (cardId, img, contentId) => {
    const x = await img.split("images%2F")[1];
    const y = await x.split("?alt=media&token=")[0];
    const storage = await getStorage();
    const desertRef = await ref(storage, `images/${y}`);

    deleteObject(desertRef)
      .then(() => {
        console.log("bir resim silindi");
        getVideoUrl(cardId, contentId);
      })
      .catch((error) => {
        console.log("resim silinirken hata : ", error);
      });
  };

  const getVideoUrl = async (cardId, contentId) => {
    const docRef = doc(db, "contents", contentId);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      deleteVideo(cardId, docSnap.data(), contentId);
    } else {
      // doc.data() will be undefined in this case
      console.log("No such document!");
    }
  };
  const deleteVideo = async (cardId, obj, contentId) => {
    const url = obj.video;
    const x = await url.split("videos%2F")[1];
    const y = await x.split("?alt=media&token=")[0];
    const storage = await getStorage();
    const desertRef = await ref(storage, `videos/${y}`);

    deleteObject(desertRef)
      .then(() => {
        console.log("bir video silindi");
        deleteDocument(contentId, cardId);
      })
      .catch((error) => {
        console.log("resim silinirken hata : ", error);
      });
  };
  const deleteDocument = async (contentId, cardId) => {
    const cardDoc = doc(db, "cards", cardId);
    const contentDoc = doc(db, "contents", contentId);
    await deleteDoc(cardDoc);
    await deleteDoc(contentDoc);
    window.location.reload();
  };

  return (
    <>
      <div className="add-post-container">
        <div
          style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            display: ilerleme ? "flex" : "none",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <LoadingAnimation color="darkorange" animType={1} size={200} />
        </div>
        <div
          style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            display: error ? "flex" : "none",
            justifyContent: "center",
            alignItems: "center",
            color: "red",
            fontSize: "144px",
          }}
        >
          ERROR
        </div>

        <div className="post-view">
          <div>
            <div>cardTitle ::: {cardTitle}</div>
            <div>cardImg :::{cardImg.name}</div>
            <div>cardIcon ::: {cardIcon}</div>
            <div>cardCategory ::: {category}</div>
            <div>video ::: {video !== null && video.name}</div>
            contents :::
            {content.length > 1
              ? content.map((i, index) => (
                  <div key={index}>
                    {"TAG: " +
                      i.tag +
                      ", " +
                      "CLASS: " +
                      i.class +
                      ", " +
                      "CONTENT: " +
                      i.content}
                  </div>
                ))
              : content.length === 1
              ? "TAG: " +
                content[0].tag +
                ", " +
                "CLASS: " +
                content[0].class +
                ", " +
                "CONTENT: " +
                content[0].content
              : ""}
          </div>

          <button
            style={{
              height: "100px",
              backgroundColor: "var(--color-yesil)",
              margin: "2em",
            }}
            onClick={() => dbKaydet()}
          >
            VERİTABANINA KAYDET
          </button>
        </div>
        <div className="add-post">
          <div className="add-post-area">
            <textarea
              ref={baslikRef}
              rows={2}
              type="text"
              name="name"
              placeholder="başlık ekle"
            />
            <button onClick={() => baslikEkle()}>EKLE</button>
          </div>
          <div className="add-post-area">
            CARD IMAGE
            <input type="file" onChange={(e) => fileSelectedHandler(e)} />
          </div>
          <div className="add-post-area">
            <input
              type="checkbox"
              id="react"
              value="react"
              name="react"
              checked={reactIconSelect}
              onChange={(e) => checkboxChange(e.target.value)}
            />
            React Icon
            <input
              type="checkbox"
              id="css"
              value="css"
              name="css"
              checked={cssIconSelect}
              onChange={(e) => checkboxChange(e.target.value)}
            />
            Css Icon
            <button onClick={() => cardIconEkle()}>CARD ICON EKLE</button>
          </div>
          <div className="add-post-area">
            <div onChange={(e) => categoryChange(e)}>
              <input type="radio" value="css" name="category" />
              css
              <input type="radio" value="react" name="category" />
              react
              <input type="radio" value="style" name="category" />
              style
              <input type="radio" value="native" name="category" />
              native
            </div>
            <button onClick={() => categoryEkle()}>KATEGORİ EKLE</button>
          </div>
          <div className="add-post-area">
            VİDEO
            <input type="file" onChange={(e) => videoSetState(e)} />
          </div>
          NOT::: video adında ?, boşluk ve türkçe karakter olmamalı, daha
          önceden kaydedilmiş bir video ile adları çakışmamalıdır.
          <div className="add-post-area">
            <textarea
              ref={paragrafRef}
              rows={10}
              type="text"
              name="name"
              placeholder="pargraf ekle"
            />

            <button onClick={() => addItem(paragrafRef)}>EKLE</button>
          </div>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontSize: "20px",
            }}
          >
            <div onChange={(e) => fontSizeChange(e)}>
              <input type="radio" value="p-size-sm" name="fontSize" />
              14px
              <input type="radio" value="p-size-md" name="fontSize" />
              16px
              <input type="radio" value="p-size-lg" name="fontSize" />
              18px
              <input type="radio" value="p-size-xl" name="fontSize" />
              24px
            </div>
            <input
              type="checkbox"
              id="react"
              value="p-midle"
              name="p-midle"
              checked={pMidle}
              onChange={() => setPMidle(!pMidle)}
            />
            paragraf ortada
            <input
              type="checkbox"
              id="css"
              value="post-sub-title"
              name="not"
              checked={pNot}
              onChange={() => setPNot(!pNot)}
            />
            post not // wwww //
          </div>
          NOT::: Link oluşturmak için : "!!!to*%*yazı!!!" === to=url örneğin
          anasayfa için /, yazı=paragraf içindeki vurgulu kısım
          <div className="add-post-area">
            <button onClick={() => addItem(boslukRef)}>BOŞLUK EKLE</button>
          </div>
          <div className="add-post-area">
            <textarea
              ref={codeRef}
              rows={12}
              type="text"
              placeholder="Code ekle"
            />
            <button onClick={() => addItem(codeRef)}>EKLE</button>
          </div>
        </div>
      </div>
      <div className="allPosts">
        {cards.map((item) => (
          <div key={item.id} className="allPostContainer">
            <div className="allPostsIc">
              <p className="allPostsIcP">{item.title}</p>
              <button
                onClick={() => postSil(item.id, item.img, item.content)}
                className="allPostsIcBtn"
              >
                Sil
              </button>
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default AddPost;
