import React, { useEffect, useState } from "react";
import PostContent from "../contentPost/PostContent";
import LoadingAnimation from "../loadingAnimation/LoadingAnimation";
import { db } from "../../firebase-config";
import { doc, getDoc } from "@firebase/firestore";

const Post = () => {
  const [isLoading, setLoading] = useState(true);
  const [post, setPost] = useState({});

  useEffect(() => {
    const x = async () => {
      const id = sessionStorage.getItem("page");
      const docRef = await doc(db, "cards", id);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        const title = await docSnap.data().title;
        const idContent = await docSnap.data().content;
        const docRefContent = await doc(db, "contents", idContent);
        const docSnapContent = await getDoc(docRefContent);
        if (docSnapContent.exists()) {
          await setPost({
            title: title,
            video: docSnapContent.data().video,
            content: docSnapContent.data().content,
          });
          setLoading(false);
        } else {
          console.log("No such document!");
        }
      } else {
        console.log("No such document!");
      }
    };
    x();
  }, []);

  if (isLoading) {
    return (
      <div
        style={{
          position: "absolute",
          width: "100%",
          height: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <LoadingAnimation color="darkorange" animType={1} size={200} />
      </div>
    );
  }
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <PostContent post={post} />
    </div>
  );
};

export default Post;
