import React, { useState, useEffect } from "react";
import "./howToDoCard.scss";
import { SiReactos, SiCsswizardry } from "react-icons/si";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";

const HowToDoCard = ({ cardImg, cardTitle, cardIcon, id }) => {
  const [url, setUrl] = useState();
  useEffect(() => {
    const createUrl = async (cardTitle, id) => {
      const withoutSpacesTitle = await cardTitle
        .trim()
        .replace(/\s+/g, "-")
        .toLowerCase();
      const createdUrl = await (withoutSpacesTitle + "-" + id.toString());
      setUrl(createdUrl);
    };
    createUrl(cardTitle, id);
  }, [cardTitle, id]);
  const urlSession = () => {
    sessionStorage.setItem("page", id);
  };
  return (
    <NavLink
      to={`/how-to-do/${url}`}
      className="how-to-do-card"
      onClick={() => urlSession()}
    >
      <div
        className="how-to-do-card-image"
        style={{
          "--bg-img": `url(${cardImg})`,
        }}
      ></div>
      <div className="how-to-do-card-title">{cardTitle.trim()}</div>
      <div className="how-to-do-card-title-icon-container">
        <SiReactos
          style={{
            display:
              cardIcon === 1 ? "block" : cardIcon === 3 ? "block" : "none",
          }}
          size={20}
          className="how-to-do-card-title-icon"
        />
        <SiCsswizardry
          style={{
            display:
              cardIcon === 2 ? "block" : cardIcon === 3 ? "block" : "none",
          }}
          size={20}
          className="how-to-do-card-title-icon"
        />
      </div>
    </NavLink>
  );
};
HowToDoCard.propTypes = {
  id: PropTypes.string.isRequired,
  cardImg: PropTypes.string.isRequired,
  cardTitle: PropTypes.string.isRequired,
  cardIcon: PropTypes.number.isRequired,
};
HowToDoCard.defaultProps = {
  id: "",
  cardImg: "",
  cardTitle: "Özgür Küpeli",
  cardIcon: 0,
};
export default HowToDoCard;
