import React, { useEffect, useState } from "react";
import "./howToDo.scss";
import HowToDoHeader from "../howToDoHeader/HowToDoHeader";
import HowToDoCard from "../howTodoCard/HowToDoCard";
import { useRecoilState } from "recoil";
import categoryAtom from "../../atoms/categoryAtom";
import { db } from "../../firebase-config";
import { collection, getDocs } from "@firebase/firestore";
import LoadingAnimation from "../loadingAnimation/LoadingAnimation";

const HowToDo = () => {
  const [selectedCategory, setSelectedCategory] = useRecoilState(categoryAtom);
  //selectedCategory her değiştiğinde session'a atıyorum ki tarayıcıdan yenileyince seçili kategori sıfırlanmasın,
  //atomda session'a bakıp ilk değer hesaplanıyor.

  const [cards, setCards] = useState([]);
  useEffect(() => {
    const postsCollectionsRef = collection(db, "cards");
    const getPosts = async () => {
      const data = await getDocs(postsCollectionsRef);
      await setCards(data.docs.map((i) => ({ ...i.data(), id: i.id })));
    };
    getPosts();
  }, []);
  useEffect(() => {
    if (selectedCategory === 0) {
      sessionStorage.setItem("selectedCategory", "all");
    }
    if (selectedCategory === 1) {
      sessionStorage.setItem("selectedCategory", "css");
    }
    if (selectedCategory === 2) {
      sessionStorage.setItem("selectedCategory", "react");
    }
    if (selectedCategory === 3) {
      sessionStorage.setItem("selectedCategory", "style");
    }
    if (selectedCategory === 4) {
      sessionStorage.setItem("selectedCategory", "native");
    }
  }, [selectedCategory]);

  const contentIconHesapla = (string) => {
    // 1-react 2-css 3-react,css ikonları olarak belirlendi
    if (string.length > 5) {
      return 3;
    }
    if (string.length === 5) {
      return 1;
    }
    return 2;
  };

  const dataListFilterFunc = (data) => {
    if (selectedCategory === 1) {
      let arr = data.filter((i) => {
        return i.category === "css";
      });
      return arr;
    }
    if (selectedCategory === 2) {
      let arr = data.filter((i) => {
        return i.category === "react";
      });
      return arr;
    }
    if (selectedCategory === 3) {
      let arr = data.filter((i) => {
        return i.category === "style";
      });
      return arr;
    }
    if (selectedCategory === 4) {
      let arr = data.filter((i) => {
        return i.category === "native";
      });
      return arr;
    }

    return data;
  };

  if (cards.length < 1) {
    return (
      <div
        style={{
          position: "absolute",
          width: "100%",
          height: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <LoadingAnimation color="darkorange" animType={5} size={200} />
      </div>
    );
  }

  return (
    <div className="content-box">
      <HowToDoHeader
        dropdownSelected={selectedCategory}
        setDropdownSelected={setSelectedCategory}
      />
      <div className="content-content-box">
        {dataListFilterFunc(cards).map((i) => (
          <HowToDoCard
            key={i.id}
            id={i.id}
            cardImg={i.img}
            cardTitle={i.title}
            cardIcon={contentIconHesapla(i.icon)}
          />
        ))}
      </div>
    </div>
  );
};

export default HowToDo;
