import React, { useRef } from "react";
import "./home.scss";
import { AiTwotoneMail, AiFillGithub, AiFillLinkedin } from "react-icons/ai";

const Home = () => {
  const bigImage = useRef(null);
  const smallImage = useRef(null);

  const onImageLoad = () => {
    smallImage.current.style.display = "none";
    bigImage.current.style.display = "block";
  };

  return (
    <div className="homepage">
      <div className="home-card">
        <div className="text">
          <div id="hello">Hello!</div>
          <div id="ozgur">I'm ÖZGÜR.</div>
          <div className="yazi">
            I'm a front-end developer. I write code to create interactive
            screens using React.js (JS, HTML, CSS/Sass). I develop web and
            mobile applications.
          </div>
          <div className="iletisim">
            <p>Contact</p>
            <div className="iletisim-container">
              <div className="iletisim-link-bir">
                <AiTwotoneMail
                  className="iletisim-link-icon"
                  size={20}
                  color="black"
                />
                <p className="iletisim-link-a">ozgurkupeli@aol.com</p>
              </div>
              <div className="iletisim-link-iki">
                <AiFillGithub
                  className="iletisim-link-icon"
                  size={20}
                  color="black"
                />
                <a
                  href="https://github.com/Ozgur-Kupeli"
                  target="_blank"
                  rel="noreferrer"
                  className="iletisim-link-a iletisim-link-b"
                >
                  GitHub
                </a>
              </div>
              <div className="iletisim-link-uc">
                <AiFillLinkedin
                  className="iletisim-link-icon"
                  size={20}
                  color="black"
                />
                <a
                  href="https://www.linkedin.com/in/özgür-küpeli-376587209"
                  target="_blank"
                  rel="noreferrer"
                  className="iletisim-link-a iletisim-link-b"
                >
                  LinkedIn
                </a>
              </div>
            </div>
          </div>
        </div>

        <div className="resim">
          <img
            ref={bigImage}
            onLoad={onImageLoad}
            className="img2"
            src="./1.png"
            alt="profileImage"
          />
          <img
            ref={smallImage}
            className="img1"
            src="./1small.png"
            alt="profileImage"
          />
        </div>
      </div>
    </div>
  );
};

export default Home;
