import React from "react";
import "./sidebar.scss";
import { NavLink } from "react-router-dom";

const Sidebar = ({ selectedNav, selectedFunc, isOpen }) => {
  return (
    <div
      className="sidebar"
      style={{
        transformOrigin: " 100% 0%",
        transform: isOpen ? "scale(1,1)" : "scale(0,0)",
      }}
    >
      <ul className="side-nav">
        <li
          className={`side-item ${selectedNav === 1 && "selectedSidebarItem"}`}
        >
          <NavLink className="side-link" to="/" onClick={() => selectedFunc(1)}>
            Who Am I
          </NavLink>
        </li>
        <li
          className={`side-item ${selectedNav === 2 && "selectedSidebarItem"}`}
        >
          <NavLink
            className="side-link"
            to="/how-to-do"
            onClick={() => selectedFunc(2)}
          >
            How To Do
          </NavLink>
        </li>
      </ul>
    </div>
  );
};

export default Sidebar;
