import React, { useEffect, useState } from "react";
import "./loadingAnimation.scss";
import PropTypes from "prop-types";

const LoadingAnimation = ({ color, animType, size }) => {
  const [type, setType] = useState({});
  useEffect(() => {
    switch (animType) {
      case 2:
        setType({
          duration: 1,
          startStop: 0.1,
        });
        break;
      case 3:
        setType({
          duration: 0.7,
          startStop: 0.1,
        });
        break;
      case 4:
        setType({
          duration: 0.3,
          startStop: 0.1,
        });
        break;
      case 5:
        setType({
          duration: 1,
          startStop: 0.001,
        });
        break;
      case 6:
        setType({
          duration: 2,
          startStop: 0.005,
        });
        break;
      default:
        setType({
          duration: 2,
          startStop: 0.1,
        });
        break;
    }
  }, [animType]);
  return (
    <div className="anim-container">
      <div
        className="anim-loader"
        style={{
          "--loader-color": `${color}`,
          "--width": `${size}px`,
          "--duration": `${type.duration}s`,
          "--start-stop": `${type.startStop}s`,
        }}
      >
        <span style={{ "--i": "1" }}></span>
        <span style={{ "--i": "2" }}></span>
        <span style={{ "--i": "3" }}></span>
        <span style={{ "--i": "4" }}></span>
        <span style={{ "--i": "5" }}></span>
        <span style={{ "--i": "6" }}></span>
        <span style={{ "--i": "7" }}></span>
        <span style={{ "--i": "8" }}></span>
        <span style={{ "--i": "9" }}></span>
        <span style={{ "--i": "10" }}></span>
        <span style={{ "--i": "11" }}></span>
        <span style={{ "--i": "12" }}></span>
        <span style={{ "--i": "13" }}></span>
        <span style={{ "--i": "14" }}></span>
        <span style={{ "--i": "15" }}></span>
        <span style={{ "--i": "16" }}></span>
        <span style={{ "--i": "17" }}></span>
        <span style={{ "--i": "18" }}></span>
        <span style={{ "--i": "19" }}></span>
        <span style={{ "--i": "20" }}></span>
      </div>
    </div>
  );
};

LoadingAnimation.propTypes = {
  color: PropTypes.string.isRequired,
  animType: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
};
LoadingAnimation.defaultProps = {
  color: "",
  animType: 0,
  size: 100,
};

export default LoadingAnimation;
